table {
  font: 14px/1.4 Georgia, Serif;
}

#page-wrap {
  margin: 50px;
}

table p {
  margin: 20px 0;
}

table {
  width: 100%;
  border-collapse: collapse;
}

tr:nth-of-type(odd) {
  background: rgba(0, 0, 0, .2);
}

th {
  background: #333;
  color: white;
  font-weight: bold;
}

td, th {
  padding: .5rem 1rem;
  border: 1px solid #ccc;
  text-align: left;
}